*, :before, :after {
  box-sizing: border-box;
}

:root {
  --primary-color: #4285f4;
  --secondary-color: #0f9d58;
  --tertiary-color: #db4437;
  --quaternary-color: #f4b400;
  --background-color: #fafafa;
  --foreground-color: #fff;
  --text-color-primary: #212121;
  --text-color-secondary: #757575;
  --border-color: #e0e0e0;
  --shadow-light: 0 1px 3px #0000001f, 0 1px 2px #0000003d;
  --shadow-medium: 0 3px 6px #00000029, 0 3px 6px #0000003b;
  --transition-speed: .3s;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.5;
  overflow-x: hidden;
}

body {
  color: var(--text-color-primary);
  background-color: var(--background-color);
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.main-layout {
  background-color: var(--background-color);
  flex-direction: column;
  width: 100%;
  max-width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.app-header {
  text-align: center;
  background-color: var(--foreground-color);
  box-shadow: var(--shadow-light);
  z-index: 10;
  margin-bottom: 0;
  padding: 15px 0;
  position: relative;
}

.app-header h1 {
  color: var(--primary-color);
  margin: 0;
  font-size: 1.8rem;
  font-weight: 400;
}

.app-header p {
  color: var(--text-color-secondary);
  margin: 5px 0 0;
  font-size: .95rem;
}

.app-main {
  flex-direction: column;
  flex: 1;
  width: 100%;
  padding: 0;
  display: flex;
  position: relative;
  overflow-y: auto;
}

.app-footer {
  text-align: center;
  color: var(--text-color-secondary);
  border-top: 1px solid var(--border-color);
  background-color: var(--foreground-color);
  padding: 10px 0;
  font-size: .85rem;
}

.app-footer a {
  color: var(--primary-color);
  text-decoration: none;
}

.app-footer a:hover {
  text-decoration: underline;
}

.app-section {
  background-color: var(--foreground-color);
  box-shadow: var(--shadow-light);
  transition: box-shadow var(--transition-speed);
  border-radius: 4px;
  margin-bottom: 8px;
  padding: 15px;
}

.app-section:hover {
  box-shadow: var(--shadow-medium);
}

.section-title {
  color: var(--text-color-primary);
  border-bottom: 1px solid var(--border-color);
  margin-top: 0;
  margin-bottom: 15px;
  padding-bottom: 8px;
  font-size: 1.2rem;
  font-weight: 500;
}

.section-content {
  padding: 5px 0;
}

.two-column-layout {
  flex: 1;
  grid-template-columns: 350px 1fr;
  gap: 10px;
  width: 100%;
  padding: 10px;
  display: grid;
}

@media (width <= 768px) {
  .two-column-layout {
    grid-template-columns: 1fr;
  }
}

.simulation-controls {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.control-group {
  flex-direction: column;
  margin-bottom: 12px;
  display: flex;
}

.control-group label {
  color: var(--text-color-secondary);
  margin-bottom: 5px;
  font-size: .9rem;
  font-weight: 500;
}

.control-group input[type="number"], .control-group input[type="text"] {
  border: 1px solid var(--border-color);
  transition: border-color var(--transition-speed);
  border-radius: 4px;
  padding: 8px 10px;
  font-size: .9rem;
}

.control-group input[type="number"]:focus, .control-group input[type="text"]:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #4285f433;
}

.radio-group {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.radio-group label {
  cursor: pointer;
  align-items: center;
  font-weight: normal;
  display: flex;
}

.radio-group input[type="radio"] {
  accent-color: var(--primary-color);
  margin-right: 8px;
}

.button-group {
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  transition: background-color var(--transition-speed), transform var(--transition-speed);
  box-shadow: var(--shadow-light);
  text-transform: uppercase;
  letter-spacing: .5px;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: .9rem;
  font-weight: 500;
}

button:hover {
  box-shadow: var(--shadow-medium);
  background-color: #3367d6;
  transform: translateY(-1px);
}

button:active {
  transform: translateY(1px);
}

.reset-button {
  background-color: #757575;
}

.reset-button:hover {
  background-color: #616161;
}

.rule-matrix-editor {
  margin-top: 20px;
}

.matrix-container {
  margin: 10px 0;
  overflow-x: auto;
}

.matrix-vertical-container {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: 10px 0;
  display: flex;
}

.matrix-section {
  flex-direction: row;
  align-items: center;
  gap: 15px;
  width: 100%;
  margin-bottom: 5px;
  display: flex;
}

.matrix-section .matrix-label {
  color: var(--text-color-primary);
  text-align: right;
  width: 80px;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: bold;
}

.number-picker {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.number-picker-btn {
  color: #333;
  cursor: pointer;
  width: 20px;
  height: 28px;
  box-shadow: none;
  min-width: unset;
  text-transform: none;
  letter-spacing: normal;
  background-color: #f5f5f5;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: normal;
  transition: background-color .2s;
  display: flex;
}

.number-picker-btn:hover {
  box-shadow: none;
  background-color: #ebebeb;
  transform: none;
}

.number-picker-btn:active {
  background-color: #e0e0e0;
  transform: none;
}

.number-picker-value {
  text-align: center;
  flex: 1;
  min-width: 16px;
  padding: 0 3px;
  font-size: 14px;
  font-weight: bold;
}

.matrix-labels {
  color: var(--text-color-secondary);
  justify-content: space-around;
  width: 100%;
  margin-bottom: 5px;
  font-size: .85rem;
  font-weight: 500;
  display: flex;
}

.matrix-label-col {
  flex-direction: column;
  align-items: center;
  width: 33.33%;
  display: flex;
}

.matrix-label {
  text-align: center;
  margin-bottom: 2px;
}

.matrix-label-cells {
  justify-content: center;
  gap: 2px;
  display: flex;
}

.matrix-label-cell {
  visibility: hidden;
  width: 32px;
}

.matrix-table {
  border-collapse: collapse;
  width: 100%;
}

.matrix-table td {
  width: 32px;
  height: 32px;
  padding: 1px;
}

.matrix-table input {
  text-align: center;
  border: 1px solid var(--border-color);
  width: 40px;
  height: 40px;
  transition: background-color var(--transition-speed), border-color var(--transition-speed);
  color: var(--text-color-primary);
  border-radius: 4px;
  padding: 0;
  font-size: 1.1rem;
}

.matrix-table input:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #4285f433;
}

.left-matrix {
  background-color: #db44371a;
}

.center-matrix {
  background-color: #4285f41a;
}

.right-matrix {
  background-color: #0f9d581a;
}

.reset-matrix-button {
  background-color: #757575;
  margin-top: 10px;
}

.visualization-container {
  background-color: var(--foreground-color);
  width: 100%;
  box-shadow: var(--shadow-light);
  min-height: 200px;
  transition: box-shadow var(--transition-speed);
  border-radius: 4px;
  margin: 10px 0;
  padding: 0;
  overflow-x: auto;
}

.visualization-container:hover {
  box-shadow: var(--shadow-medium);
}

#spacetime-diagram {
  justify-content: center;
  align-items: center;
  height: calc(100vh - 500px);
  min-height: 400px;
  max-height: 85vh;
  display: flex;
}

.matrix-table input[type="number"] {
  text-align: center;
  color: var(--text-color-primary);
  -moz-appearance: textfield;
  font-size: 1.1rem;
  font-weight: bold;
}

.matrix-table input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.matrix-table input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.tooltip {
  color: #fff;
  pointer-events: none;
  z-index: 1000;
  box-shadow: var(--shadow-medium);
  background-color: #212121e6;
  border-radius: 4px;
  max-width: 250px;
  padding: 8px 12px;
  font-size: .85rem;
  position: absolute;
}
